export default {
	name: 'article-item',
	props: {
		list: {
			type: Array,
			default () {
				return [];
			}
		},
		showOperate: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			showArticleOperate: false,
			articleId: '',
			articleOffsetTop: 0,
			newList: []
		};
	},
	methods: {
		showMoreMask(e, articleId, userId, articleIndex) {
			this.$emit('showOperate', e, articleId, userId, articleIndex);
		},
		hideArticleOperate() {
			this.showArticleOperate = false;
		},
		goDetail(id, index) {
			uni.setStorageSync('articleIndex', index)
			uni.navigateTo({
				url: '/pages/home/article/detail/detail?id=' + id
			});
		},
		goCategory(id){
			uni.navigateTo({
				url:"/pages/classify/detail/detail?id="+id
			})
		}
	},
	watch: {
		list(val) {
			this.newList = val.map((el) => {
				if (el.showType == 3) {
					el.attachment.push("")
					el.attachment = el.attachment.slice(0, 3)
				}
				return el;
			})
		}
	},

};
